(function() {
  angular.module("imagewikiFrontend").directive('imageViolation', [
    '$compile', '$timeout', 'ImageModel', 'vcRecaptchaService', function($compile, $timeout, ImageModel, vcRecaptchaService) {
      return {
        restrict: 'E',
        templateUrl: 'app/components/image-violation/image-violation.html',
        scope: {
          imageId: '='
        },
        controller: function($scope, $element, $attrs, $transclude) {
          var resetViolation;
          $scope.visible = false;
          $scope.violation = {
            reporter_user_id: null,
            violation_comments: null,
            violation_reason_id: null
          };
          $scope.response = null;
          $scope.widgetId = null;
          $scope.model = {
            key: '6LfZSxITAAAAAAbbOq-2PrbETzWZVbnjOIOJOy_E'
          };
          if ($scope.$parent.$parent.currentUser) {
            $scope.violation['reporter_user_id'] = $scope.$parent.$parent.currentUser.id;
          }
          $scope.setResponse = function(response) {
            $scope.response = response;
          };
          $scope.setWidgetId = function(widgetId) {
            $scope.widgetId = widgetId;
          };
          $scope.cbExpiration = function() {
            $scope.response = null;
          };
          $scope.reportViolation = function(violation) {
            if (violation.violation_reason_id === null || violation.violation_reason_id === '') {
              $scope.$emit('showToastrMessage', {
                type: 'error',
                message: 'You need to select a reason.',
                title: 'Error'
              });
              return false;
            }
            if ($scope.response === null) {
              $scope.$emit('showToastrMessage', {
                type: 'error',
                message: 'Please check the recaptcha.',
                title: 'Are your a robot?'
              });
              return false;
            }
            ImageModel.reportViolation($scope.imageId, violation).then(function(data) {
              $scope.$emit('showToastrMessage', {
                type: 'success',
                message: data.message,
                title: 'Success'
              });
              resetViolation();
            }, function() {
              $scope.$emit('showToastrMessage', {
                type: 'error',
                message: data.message,
                title: 'Error'
              });
              resetViolation();
            });
          };
          resetViolation = function() {
            $scope.visible = false;
            $scope.violation['violation_comments'] = null;
            $scope.violation['violation_reason_id'] = null;
          };
        },
        link: function(scope, element, attr) {
          element.find('[data-toggle="tooltip"]').tooltip();
        }
      };
    }
  ]);

}).call(this);



(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  angular.module("imagewikiFrontend").directive('imagesWall', [
    '$compile', '$timeout', function($compile, $timeout) {
      return {
        restrict: 'E',
        templateUrl: 'app/components/images-wall/images-wall.html',
        scope: {
          images: '=',
          selector: '@',
          selectable: '='
        },
        controller: function($scope, $element, $attrs, $transclude) {
          $scope.selected = {};
          $scope.page = 1;
          $scope.isFirstPage = true;
          $scope.isLastPage = false;
          $scope.$on('UpdateTotalImages', function(event, args) {
            if (args.totalImages < $scope.$parent.itemsPerPage) {
              $scope.isLastPage = true;
            } else {
              $scope.isLastPage = false;
            }
          });
          $scope.$on('ClearSelectedImages', function(event) {
            $scope.selected = {};
            $scope.$parent.selected = [];
          });
          $scope.changePage = function(newPageNumber) {
            $scope.page = newPageNumber;
            if (newPageNumber === 1) {
              $scope.isFirstPage = true;
            } else {
              $scope.isFirstPage = false;
            }
            $scope.$emit('ChangeUserImagesPage', {
              page: newPageNumber
            });
          };
          $scope.selectImage = function(image) {
            var id;
            if ($scope.selectable === false) {
              return false;
            }
            id = image.image_id;
            if (indexOf.call($scope.$parent.selected, image) >= 0) {
              $scope.selected[id] = false;
              $scope.$parent.selected.remove(image);
            } else {
              $scope.selected[id] = true;
              $scope.$parent.selected.push(image);
            }
          };
          $scope["delete"] = function(image) {
            $scope.$parent["delete"](image);
          };
        },
        link: function(scope, element, attr) {
          var galleryCreated, runGallery;
          galleryCreated = false;
          runGallery = function() {};
          scope.$on('RestartGallery', function(event) {
            scope.$emit('showToastrMessage', {
              type: 'info',
              message: 'The gallery is reloading after the changes.',
              title: 'Loading Gallery',
              options: {
                closeButton: true,
                closeHtml: '<i class="fa fa-refresh"></i>'
              }
            });
          });
          scope.$on('ReloadGallery', function(event) {});
          scope.$on('ImageLoaded', function(event) {});
        }
      };
    }
  ]);

}).call(this);



