(function() {
  angular.module("imagewikiFrontend").factory('PbrSelectModel', [
    '$q', '$filter', function($q, $filter) {
      var formatRule, optionRule, options, pbrSelectModel, platform, platformRule;
      options = [
        {
          id: 'block',
          text: 'Block'
        }, {
          id: 'allow',
          text: 'Allow'
        }, {
          id: 'allow_w_attr',
          text: 'Allow with Attribution'
        }, {
          id: 'ad_rev_share',
          text: 'Ad Rev Share License'
        }
      ];
      platform = function(name) {
        return name.split('.')[1];
      };
      platformRule = function(rules, name) {
        var formatedRule, i, key, len, quit, rule, value;
        formatedRule = '';
        quit = false;
        for (i = 0, len = rules.length; i < len; i++) {
          rule = rules[i];
          for (key in rule) {
            value = rule[key];
            if (key === platform(name)) {
              formatedRule = rule;
              quit = true;
            }
          }
          if (quit) {
            break;
          }
        }
        return formatedRule;
      };
      optionRule = function(name, rule) {
        return $filter('filter')(options, {
          id: rule[platform(name)]
        })[0];
      };
      formatRule = function(name, value) {
        var newRule;
        newRule = {};
        newRule[platform(name)] = value.id;
        return newRule;
      };
      pbrSelectModel = {};
      pbrSelectModel.options = options;
      pbrSelectModel.notChanged = function(name, initial, model) {
        return angular.equals(initial, pbrSelectModel.formatInitialValue(name, model));
      };
      pbrSelectModel.formatInitialValue = function(name, value) {
        var formated, rule;
        if (value === null) {
          return {
            id: '',
            text: ''
          };
        }
        rule = platformRule(value, name);
        formated = optionRule(name, rule);
        return formated;
      };
      pbrSelectModel.formatValue = function(name, value, originalValue) {
        var i, k, len, newRules, rule, v;
        newRules = [];
        if (originalValue === null) {
          originalValue = [];
        }
        if (originalValue.length === 0) {
          newRules.push(formatRule(name, value));
        } else {
          for (i = 0, len = originalValue.length; i < len; i++) {
            rule = originalValue[i];
            for (k in rule) {
              v = rule[k];
              if (k === platform(name)) {
                newRules.push(formatRule(name, value));
              } else {
                newRules.push(rule);
              }
            }
          }
        }
        return newRules;
      };
      pbrSelectModel.formatLabel = function(name, value) {
        var rule;
        if (value.id != null) {
          rule = value;
        } else {
          rule = pbrSelectModel.formatInitialValue(name, value);
        }
        return rule.text;
      };
      return pbrSelectModel;
    }
  ]);

}).call(this);



