(function() {
  angular.module("imagewikiFrontend").controller("EditableFieldController", [
    '$scope', '$element', '$attrs', 'EditableFieldFactory', function($scope, $element, $attrs, EditableFieldFactory) {
      var initial, ref, showLabel;
      $scope.block = $scope.block || 'div';
      $scope.textInput = (ref = $scope.type) === 'text' || ref === 'password' || ref === 'email';
      if ($scope.type === 'date') {
        $scope.datepicker_name = $scope.name + "-datepicker";
      }
      $scope.label = EditableFieldFactory.formatLabel($scope.type, $scope.name, $scope.ngModel);
      initial = EditableFieldFactory.formatInitialValue($scope.name, angular.copy($scope.ngModel));
      $scope.value = {
        initial: initial
      };
      if ($scope.type === 'array') {
        $scope.value.tags = $.unique(angular.copy(initial));
      }
      $scope.$watch('value.initial', function() {
        if (EditableFieldFactory.notChanged($scope.name, $scope.value.initial, $scope.ngModel)) {
          return false;
        }
        $scope.ngModel = EditableFieldFactory.formatValue($scope.name, angular.copy($scope.value.initial), $scope.ngModel);
        $scope.label = EditableFieldFactory.formatLabel($scope.type, $scope.name, $scope.value.initial);
        $scope.$emit('imageChanged');
      });
      $scope.$on('beginImageEdition', function(event, edit) {
        if (edit) {
          $scope.showEdit();
        } else {
          showLabel();
        }
      });
      $scope.updateTags = function($tag) {
        var tags;
        tags = [];
        angular.forEach($scope.value.tags, function(value, key) {
          tags.push(value.text);
        });
        $scope.value.initial = tags;
      };
      $scope.onTimeSet = function(newDate, oldDate) {
        $scope.value.initial = moment(newDate).format('YYYY/MM/DD');
      };
      $scope.showField = $scope.$parent.showFields || false;
      $scope.showEdit = function() {
        $scope.showField = true;
      };
      showLabel = function() {
        $scope.showField = false;
      };
      $scope.cancelEdit = function() {
        $scope.value = {
          initial: angular.copy($scope.ngModel)
        };
        $scope.label = EditableFieldFactory.formatLabel($scope.type, $scope.name, $scope.value.initial);
        $scope.$broadcast('cancelEdit');
        showLabel();
      };
      $scope.saveEdit = function() {
        showLabel();
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("imagewikiFrontend").factory('EditableFieldFactory', [
    '$filter', 'ImageSelects', 'PbrSelectModel', function($filter, ImageSelects, PbrSelectModel) {
      var cleanArray, editableFieldFactory;
      editableFieldFactory = {};
      cleanArray = function(value) {
        return value.filter(function(v) {
          return v !== null;
        });
      };
      editableFieldFactory.notChanged = function(name, initial, model) {
        switch (name) {
          case 'license_type_id':
            if (initial == null) {
              return true;
            }
            return angular.equals(initial.text, model);
          case 'platform_business_rules.all':
            return PbrSelectModel.notChanged(name, initial, model);
          case 'keywords':
            return angular.equals(initial, cleanArray(model));
          default:
            return angular.equals(initial, model);
        }
      };
      editableFieldFactory.formatInitialValue = function(name, value) {
        var values;
        switch (name) {
          case 'license_type_id':
            values = ImageSelects.getSelectOptions(name);
            return $filter('filter')(values, {
              text: value
            })[0];
          case 'platform_business_rules.all':
            return PbrSelectModel.formatInitialValue(name, value);
          case 'keywords':
            return cleanArray(value);
          default:
            return value;
        }
      };
      editableFieldFactory.formatValue = function(name, value, originalValue) {
        if (value == null) {
          return '';
        }
        switch (name) {
          case 'license_type_id':
            return value.id;
          case 'platform_business_rules.all':
            return PbrSelectModel.formatValue(name, value, originalValue);
          default:
            return value;
        }
      };
      editableFieldFactory.formatLabel = function(type, name, value) {
        if (value == null) {
          return '';
        }
        if (name === 'license_type_id') {
          if (angular.isObject(value)) {
            return value.text;
          } else {
            return value;
          }
        }
        if (name === 'platform_business_rules.all') {
          return PbrSelectModel.formatLabel(name, value);
        }
        switch (type) {
          case 'boolean':
            if (value) {
              return 'YES';
            } else {
              return 'NO';
            }
            break;
          case 'date':
            return moment(value).format('DD/MM/YYYY');
          case 'array':
            return value.join(', ');
          default:
            return value;
        }
      };
      return editableFieldFactory;
    }
  ]);

}).call(this);



