      $scope.saved = true;
      images = TempImageModel.getImages();
      for (index = i = 0, len = images.length; i < len; index = ++i) {
        img = images[index];
        if (img.image_id === $stateParams.hashid) {
          $scope.index = index;
          $scope.image = img;
          $scope.originalImage = angular.copy(img);
          break;
        }
      }
      $scope.$on('imageChanged', function() {
        $scope.saved = false;
      });
      $scope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
        if (toState.name === 'login') {
          return false;
        }
        if (!$scope.saved) {
          if (!confirm('There are unsaved changes on the image. Are you sure you want to leave this page?')) {
            event.preventDefault();
          }
        }
      });
      $scope.toggleEdition = function() {
        $scope.editing = !$scope.editing;
        $scope.$broadcast('beginImageEdition', $scope.editing);
      };
      $scope.updateImage = function(image) {
        if (angular.equals(image, $scope.originalImage)) {
          $scope.$emit('showToastrMessage', {
            type: 'warning',
            message: 'No need of saving',
            'The image is untouched.': 'The image is untouched.'
          });
          return false;
        }
        $scope.image = image;
        TempImageModel.updateImage($scope.index, $scope.image);
        $scope.$emit('showToastrMessage', {
          type: 'warning',
          message: 'The data you entered was saved only locally. After you <strong>log in</strong> you can save your image on our database'
        });
        $scope.saved = true;
        $scope.toggleEdition();
      };
