(function() {
  angular.module("imagewikiFrontend").controller("ImagesController", [
    '$scope', '$state', '$stateParams', 'ImageModel', 'ImagePromise', function($scope, $state, $stateParams, ImageModel, ImagePromise) {
      $scope.image = {};
      $scope.saved = true;
      $scope.editing = false;
      $scope.image = ImagePromise.image;
      $scope.originalImage = angular.copy(ImagePromise.image);
      $scope.$on('imageChanged', function() {
        $scope.saved = false;
      });
      $scope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
        if (toState.name === 'login') {
          return false;
        }
        if (!$scope.saved) {
          if (!confirm('There are unsaved changes on the image. Are you sure you want to leave this page?')) {
            event.preventDefault();
          }
        }
      });
      $scope.toggleEdition = function() {
        $scope.editing = !$scope.editing;
        $scope.$broadcast('beginImageEdition', $scope.editing);
      };
      $scope.updateImage = function(image) {
        var fields;
        if (angular.equals(image, $scope.originalImage)) {
          $scope.$emit('showToastrMessage', {
            type: 'warning',
            message: 'No need of saving',
            title: 'The image is untouched.'
          });
          return false;
        }
        fields = {};
        fields.image_id = image.image_id;
        angular.forEach(image, function(value, key) {
          if (!angular.equals(value, $scope.originalImage[key])) {
            fields[key] = value;
          }
        });
        ImageModel.updateImage(fields).then(function(data) {
          $scope.saved = true;
          $scope.$emit('showToastrMessage', {
            type: 'success',
            message: 'Image updated.',
            title: 'Success'
          });
          $scope.toggleEdition();
          $scope.originalImage = angular.copy(image);
        }, function() {
          $scope.$emit('showToastrMessage', {
            type: 'error',
            message: 'Something went wrong! Please contact our support team.',
            title: 'Error'
          });
        });
      };
      $scope.resetImage = function() {
        $scope.image = angular.copy($scope.originalImage);
        $scope.saved = true;
      };
    }
  ]);

}).call(this);



