(function() {
  angular.module("imagewikiFrontend").controller('ApplicationController', [
    '$scope', '$state', 'toastr', 'UserAuth', 'AUTH_EVENTS', 'MessageFormatter', 'TempImageModel', function($scope, $state, toastr, UserAuth, AUTH_EVENTS, MessageFormatter, TempImageModel) {
      $scope.currentUser = UserAuth.getUser();
      $scope.$watch('currentUser', function() {});
      $scope.container_class = 'container';
      $scope.isAuthenticated = function() {
        return UserAuth.isAuthenticated();
      };
      $scope.setCurrentUser = function(user) {
        $scope.currentUser = user;
      };
      $scope.$on('showToastrMessage', function(event, data) {
        var options, title;
        options = data.options || null;
        title = data.title || null;
        toastr[data.type](data.message, title, options);
      });
      $scope.$on('reAuthenticate', function(event, data) {
        toastr.error(data.message, 'Error');
        UserAuth.logout();
        $scope.setCurrentUser(null);
        $state.go('login');
      });
      $scope.$on('resetContainer', function(event) {
        $scope.container_class = 'container';
      });
      $scope.$on('fluidContainer', function(event) {
        $scope.container_class = 'container-fluid';
      });
      $scope.$on(AUTH_EVENTS.loginSuccess, function(event) {
        toastr.success('Successfully logged in.', 'Success');
        if (TempImageModel.hasImages()) {
          toastr.info("You have images unsaved. Now that you're logged in we will save them on our servers.", 'Uploading unsaved images', {
            timeOut: 20000
          });
          TempImageModel.uploadTempImages();
        }
      });
      $scope.$on(AUTH_EVENTS.logoutSuccess, function(event) {
        toastr.success('Successfully logged out.', 'Success');
      });
      $scope.$on(AUTH_EVENTS.loginFailed, function(event, data) {
        var error_message;
        error_message = MessageFormatter.error(data);
        toastr.error(error_message, 'Error');
      });
    }
  ]);

}).call(this);



